export const signup = async (req: Request, res: Response) => {
    try {
        const bodyData = req.body;
        const info = await User.create(bodyData);
        const resData = info.toJSON();
        if (resData) {
            res.json({
                status:"success",
                message:"Signup Successfully",
                data: {
                    id:info.id,
                    name:resData.name,
                    email:resData.email,
                }
            })
        }
        else{

        }

    } catch (err) {
        console.error("Sql Error",err);
        res.json({
                status:"failed",
                message:"email alredy exits"
                }
            )
    }
}

export const getData = (req: Request, res: Response, next: NextFunction) => {
    const { name, age } = req.query;
    res.json({
        data: name
    })
}

export const authLogin = async (req: Request, res: Response, next: NextFunction) => {
    //const { email, password } = req.body;
    const getUser = await User.findOne({where:{id:2}});
    res.json({
       getUser
    })
}

export const getDataByParam = (req: Request, res: Response, next: NextFunction) => {
    const { id } = req.params;
    res.json({
        data: id
    })
}

export const formDataHandle = (req: Request<{}, {}, User>, res: Response) => {
    try {
        const bodyData: User = req.body;
        res.json({
            allData: bodyData
        })
    } catch (err) {

    }
}